.science-info__title {
  font-size: 30px;
  line-height: 36px;
  font-weight: 700;
  margin: 48px 0 24px 0;
}

.science-info__text {
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: var(--special-blue);
  margin: 0 0 24px 0;

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 30px;
  }
}

.science-info__link {
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  color: var(--basic-pink);

  @media (min-width: $desktop-width) {
    font-size: 18px;
    line-height: 30px;
  }
}

.science-info {
  order: 3;
  display: none;

  @media (min-width: $tablet-width) {
    display: flex;
  }

  @media (min-width: $desktop-width) {
    background-color: var(--special-grey-border);
    margin-top: 41px;
  }
}

.science-info__block {
  padding-left: 322px;
  position: relative;
  margin: 84px 0 150px 0;
}

.science-info__title::after {
  content: "";
  position: absolute;
  background-image: url("../../icons/sprite.svg#timer-tablet");
  background-repeat: no-repeat;
  width: 238px;
  height: 276px;
  top: -29px;
  left: 16px;

  @media (min-width: $desktop-width) {
    background-image: url("../../icons/sprite.svg#timer");
    width: 298px;
    height: 345px;
    top: -88px;
    left: -35px;
  }
}
